:root {
	--color-white: white;
	--color-bg: #f1f1f1;
	--color-gray: #767676;
	--color-black: #242424;

	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 100vh;
	font-family: "Roboto", sans-serif;
	font-size: 16px;
	background-color: var(--color-bg);
	box-sizing: border-box;
}
.expense-form {
    display: grid;
    place-items: center;
    width: 50%;
    padding: 1em;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
  
  .expense-contain {
    display: grid;
	grid-template-columns: 1fr;
	grid-row-gap: 16px;
	width: 100%;
    place-items: center;

  }
  .expense-contain label {
    width:90%;
  }
  .expense-contain label select {
	border-radius: 8px;
	border: 0px;
	font-size: inherit;
	box-sizing: border-box;
  }
  .expense-contain label input {
    box-sizing: border-box;
    width: 100%;
    padding: 11px;
    font-size: 15px;
    border: 1px solid #d4d4d4;
    border-radius: 3px;
    box-shadow: inset 0 1px 2px #e9e9e9;
  }
  
  .expense-form label {
    font-weight: bold;
  }
  
  .expense-form input,
  .expense-form select {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .expense-form button {
    background-color: #4caf50;
    color: white;
    border: none;
    padding: 10px 16px;
    font-size: 16px;
    cursor: pointer;
  }
  /* Add more CSS styles as needed */